import "./styles.scss";
import { useEffect, useState } from "react";
import Switch from "react-switch";

import {
  initialTeams,
  initialFixtures,
  currentVersion,
  resultsTypes
} from "./constans";
import {
  calcuateTeamsPointes,
  isJsonString,
  minifyJson,
  unminifyJson,
  confetti,
  trackEvent,
  randomIntFromInterval
} from "./utils";
import LeagueTable from "./components/LeagueTable";
import Fixtures from "./components/Fixtures";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

export default function App() {
  const [fixtures, setFixtures] = useState(initialFixtures);
  const [teams, setTeams] = useState(
    calcuateTeamsPointes(initialTeams, initialFixtures)
  );

  const [showGoals, setShowGoals] = useState(false);

  useEffect(() => {
    if (fixtures.every((f) => f.result)) {
      const hptPosition = teams.findIndex((t) => t.key === "hpt") + 1;
      trackEvent({
        action: "complete",
        category: "hpt",
        label: hptPosition
      });
      trackEvent({
        action: "complete",
        category: "complete",
        label: fixtures.reduce((a, v) => a.concat(v.result), "")
      });
      if (hptPosition === 1 || hptPosition === 2) {
        confetti({
          particleCount: 750,
          spread: 360,
          colors: ["#89CFF0", "#0047AB", "#3F00FF", "#4169E1"]
        });
      } else {
        confetti && confetti.reset();
      }

      const minified = fixtures.reduce((a, v) => {
        return a.concat(v.result);
      }, "");
    }
  }, [teams, fixtures]);

  useEffect(() => {
    setTeams(calcuateTeamsPointes(initialTeams, fixtures));
  }, [fixtures]);

  useEffect(() => {
    try {
      const qsState = atob(
        new URL(document.location).searchParams.get("state")
      );
      const fixturesFromLS = window.localStorage.getItem("fixtures");
      if (isJsonString(qsState)) {
        applyFromClient(JSON.parse(qsState));
      } else if (isJsonString(fixturesFromLS)) {
        const LSVersion =
          window.localStorage.getItem("version") === currentVersion.toString();
        if (LSVersion && fixturesFromLS.length) {
          applyFromClient(JSON.parse(fixturesFromLS));
        }
      }
    } catch (error) {
      console.error(error);
    }
  }, []);

  const applyFromClient = (clientFixtures) => {
    const unminified = unminifyJson(clientFixtures);
    const newFixtures = unminified.reduce(
      (ac, clientFixture) => {
        const initialIndex = fixtures.findIndex(
          (fixture) =>
            fixture.home === clientFixture.home &&
            fixture.round === clientFixture.round
        );
        const { result, homeGoals, awayGoals } = clientFixture;
        if (initialIndex !== -1 && result) {
          ac[initialIndex] = {
            ...fixtures[initialIndex],
            result,
            homeGoals,
            awayGoals
          };
        }
        return ac;
      },
      [...fixtures]
    );
    setFixtures(newFixtures);
  };

  const applyFixtureResult = (
    changedFixtureIdx,
    result,
    homeGoals,
    awayGoals
  ) => {
    const newFixtures = [...fixtures];
    const idx = newFixtures.findIndex(
      (f) => `${f.home}/${f.away}` === changedFixtureIdx
    );
    newFixtures[idx].result =
      result === newFixtures[idx].result && result === resultsTypes.TIE
        ? undefined
        : result;

    newFixtures[idx].homeGoals = homeGoals;
    newFixtures[idx].awayGoals = awayGoals;

    setFixtures(newFixtures);
    const minified = minifyJson(newFixtures);
    window.localStorage.setItem("fixtures", minified);
    window.localStorage.setItem("version", currentVersion);
  };

  const shareLink = new URL(document.location);
  shareLink.searchParams.set(
    "state",
    btoa(minifyJson(fixtures.filter((f) => f.result)))
  );

  const reset = () => {
    trackEvent({ action: "click", category: "button", label: "reset" });
    setFixtures(
      fixtures.map((f) => {
        return {
          ...f,
          result: undefined,
          homeGoals: undefined,
          awayGoals: undefined
        };
      })
    );
  };

  const copyShareLink = () => {
    trackEvent({ action: "click", category: "button", label: "share" });
    const isMobile = /Android|webOS|iPhone|iPad|iPod|Opera Mini/i.test(
      navigator.userAgent
    );

    if (isMobile && navigator.share) {
      navigator.share({
        text: shareLink.toString()
      });
    } else if (navigator.clipboard) {
      navigator.clipboard
        .writeText(shareLink.toString())
        .then(() => {
          toast.success("注转拽 ", {
            icon: "",
            position: "top-center",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined
          });
        })
        .catch((e) => console.error(e));
    } else {
      window.prompt(shareLink.toString());
    }
  };

  const shuffle = () => {
    trackEvent({ action: "click", category: "button", label: "shuffle" });
    const newFixtures = fixtures.map((f) => {
      const homeGoals = randomIntFromInterval(0, 3);
      const awayGoals = randomIntFromInterval(0, 3);
      const result =
        homeGoals > awayGoals
          ? resultsTypes.HOME
          : homeGoals < awayGoals
          ? resultsTypes.AWAY
          : resultsTypes.TIE;
      return { ...f, result, homeGoals, awayGoals };
    });
    setFixtures(newFixtures);
  };

  const toggleGoals = (checked) => {
    trackEvent({
      action: "click",
      category: "goals",
      label: checked.toString()
    });
    setShowGoals(checked);
  };

  return (
    <div className="App">
      <div id="table">
        <LeagueTable teams={teams} showGoals={showGoals} />
      </div>
      <button onClick={copyShareLink}>砖转祝 爪 </button>
      <button onClick={reset}>驻住</button>
      <button onClick={shuffle}>拽专</button>
      <div>
        <label>
          <Switch onChange={toggleGoals} checked={showGoals} />
          驻专砖 砖注专
        </label>
      </div>
      <div id="fixtures">
        <Fixtures
          fixtures={fixtures}
          applyFn={applyFixtureResult}
          showGoals={showGoals}
        />
      </div>
      <ToastContainer />
    </div>
  );
}
